import * as React from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import image1 from "../../assets/Images/rationale1.jpg";

import image2 from "../../assets/Images/rationale2.jpg";

import RoundedButtons from "../Buttons/RoundedButtons";

const BlogPostTemplate = () => {
  return (
    <div>
      <RoundedButtons />
      <div
        style={{
          border: "2px solid red",
          borderRadius: "100px",
          backgroundColor: "beige",
          padding: "10px",
        }}
      >
        <Box
          sx={{
            backgroundColor: "#f5f5f5",
            padding: "2rem",
            borderRadius: "4px",
          }}
        >
          <Typography variant="h4" sx={{ marginBottom: "1rem" }}>
            Internet Artwork Rationale and Critical Reflection
          </Typography>
          <Typography variant="subtitle1" sx={{ marginBottom: "1rem" }}>
            Date: May 15, 2023
          </Typography>
          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Rationale:
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Welcome to "See Her" - an interactive website that exposes the flaws
            in the South African police justice system through a chatbot
            experience. Our code aims to simulate interactions between users and
            the police station, highlighting the challenges and inefficiencies
            within the system. By incorporating elements like lost case folders,
            difficulty finding information, and the need to restart, we
            symbolize the frustrations experienced by individuals within the
            system. Our goal is to raise awareness and shed light on the broken
            aspects of the South African police justice system.
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            The Artwork: In our "Art" section, we feature powerful images of
            women covered in flowers. These images serve as a portrayal of
            deceased victims, with flowers representing their graves. Out of
            respect, we did not use images of actual victims, but their names
            are real and already in the public domain. Through these visuals, we
            aim to bring attention to the devastating impact of gender-based
            violence in South Africa.
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Tinder Game and Statistics: As part of our features, we have
            included a Tinder game that uses the names of victims from the
            @keepsameenergy Instagram page. This game tells the stories of
            victims and provides statistics for each province, highlighting the
            widespread nature of gender-based violence in South Africa. By
            incorporating this interactive element, we aim to engage users and
            create a deeper understanding of the issue.
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Koleka Putuma's Poem and Phone Calls: We also utilize Koleka
            Putuma's poem, "every 3 hours, one of us does not make it," to
            emphasize the unattended phone calls from victims. The ringing
            buttons represent the calls made to 10111 by those in need of help.
            Through this inclusion, we aim to highlight the urgency and dire
            consequences of unaddressed calls and unresponsive systems.
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Image Credits: We would like to credit the images used in our
            website to maintain transparency and respect for the artists. The
            images can be found at the following sources: - Surreal Portraits by
            Sage: https://mymodernmet.com/surreal-portraits-sage/ - Pinterest
            board: https://www.pinterest.co.uk/pin/113715959333052924/
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            By combining the chatbot experience, thought-provoking visuals,
            storytelling through the Tinder game, and the emotional impact of
            Koleka Putuma's poem, "See Her" aims to raise awareness, promote
            dialogue, and inspire action in the fight against gender-based
            violence in South Africa.
          </Typography>
          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            References
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Shadee, A. and Bernadette, M., 2017. Queer utopias and a (Feminist)
            Iranian vampire: a critical analysis of resistive monstrosity in A
            Girl Walks Home Alone at Night.
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Park, Y., 2007. Design principles for interactive user experience
            through net art.
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            THORLACIUS, L., 2007. The Role of Aesthetics in Web Design.
          </Typography>

          <Typography variant="body1" sx={{ marginBottom: "1rem" }}>
            Coleman, A., 2015. Net.art and Net.pedagogy: Introducing Internet
            Art to the Digital Art Curriculum.
          </Typography>

          <Typography variant="body1">
            Kerr, D., 2015. The Early Disruptors: 7 Masterpieces of '90s Net Art
            Everyone Should Know About.
          </Typography>

          {/* Insert the images with captions */}
          <figure>
            <img src={image1} alt="Guide 1" />
            <figcaption>
              Figure 1.1 A Scene From A Girl Walks Alone At Night (2014)
            </figcaption>
          </figure>

          <figure>
            <img src={image2} alt="Guide 2" />
            <figcaption>
              Figure 1.1 A Scene From A Girl Walks Alone At Night (2014){" "}
            </figcaption>
          </figure>

          <Typography variant="subtitle2" sx={{ marginTop: "1rem" }}>
            Written by Fanelesibonge Mbuyazi
          </Typography>
        </Box>
      </div>
    </div>
  );
};

export default BlogPostTemplate;
